import {
	staffDataGet
}
	from './api';
import {
	SaleStatus,
	GoodType,
	Dinnertime,
	isFreeze,
	isWechat,
	isAdmin,
	isDelete,
	chooseAll,
	positionRight
}
	from "@/config/cfg";
import Vue from "vue";
import {
	Util
}
	from '@/config/util';
export const treeMethods = {
	//初始化treeData
	initTreeData(treeData = []) {
		treeData = treeData.map((item) => {
			Vue.set(item, 'canEditor', false);
			Vue.set(item, 'addIcon', true);
			Vue.set(item, 'editorIcon', true);
			Vue.set(item, 'deleteIcon', true);
			Vue.set(item, "isShowBtn", false);
			Vue.set(item, 'isActive', false);
			Vue.set(item, 'breakshow', false);
			Vue.set(item, 'allShow', false);
			Vue.set(item, 'isExpand', true);
			Vue.set(item, 'isFolder', false);
			if (item.hasOwnProperty('employee_list') && item.employee_list.length > 0) {
				this.initTreeData(item.employee_list);
			}
			return item;
		});
		return treeData;
	},
	//将部门分类添加到树结构的分类中
	addTitleToTree(treeData = []) {
		let treeDataA = {};
		Vue.set(treeDataA, 'department_name', '部门管理');
		Vue.set(treeDataA, 'employee_list', treeData);
		Vue.set(treeDataA, 'department_id', '0');
		return treeDataA;
	},
	//给每一项添加一个key值，便于区分级别
	generateKey(treeData = [], parentKey) {
		treeData = treeData.map((item, i) => {
			item.key = parentKey + '-' + i.toString();
			if (item.hasOwnProperty('employee_list') && item.employee_list.length > 0) {
				this.generateKey(item.employee_list, item.key);
			}
			return item;
		});
		return treeData;
	},
	//判断分级的层数
	getKeylength(key) {
		if (!Util.isEmpty(key)) {
			let keyArr = key.split('-');
			return keyArr.length;
		}
	},
	//选中时对应显示的添加，删除和编辑
	selectIcon(treeData = []) {
		treeData = treeData.map((item) => {
			let len = this.getKeylength(item.key);
			if (len === 2) {
				item.addIcon = true;
				item.editorIcon = false;
				item.deleteIcon = false;
				item.isExpand = true;
				item.isActive = true;
			} else if (len === 3) {
				item.addIcon = false;
				item.editorIcon = true;
				item.deleteIcon = true;
				item.isExpand = false;
				item.isFolder = true;
			} else if (len === 4) {
				item.addIcon = false;
				item.editorIcon = false;
				item.deleteIcon = false;
			}
			if (item.hasOwnProperty('employee_list') && item.employee_list.length > 0) {
				this.selectIcon(item.employee_list);
			}
		});
	},
	//保存树中出现的部门名称
	saveDepartName: function (treeData = []) {
		let departNameArr = [];
		const _saveName = (item) => {
			item.forEach((items) => {
				if (items.hasOwnProperty('department_name') && items.department_name !== "部门管理") {
					departNameArr.push(items.department_name);
				}
				if (items.hasOwnProperty('employee_list') && items.employee_list.length > 0) {
					_saveName(items.employee_list);
				}
			});
		}
		_saveName(treeData);
		return departNameArr;
	},
	//根据索引对应id
	treeDataById: function (treeData = []) {
		let treeDataById = {};
		const _saveNode = (item) => {
			item.forEach((items) => {
				if (items.hasOwnProperty('department_id')) {
					treeDataById[items.department_id] = items;
				} else if (items.hasOwnProperty('employee_id')) {
					treeDataById[items.employee_id] = items;
				}
				if (items.hasOwnProperty('employee_list') && items.employee_list.length > 0) {
					_saveNode(items.employee_list);
				}
			});
		};
		_saveNode(treeData);
		return treeDataById;
	},
	//切换点击时的样式
	changeSelecte(node = {}, treeDataById = {}) {
		let id = node.department_id ? node.department_id : node.employee_id;
		for (let item in treeDataById) {
			if (treeDataById.hasOwnProperty(item)) {
				if (id === item) {
					treeDataById[item].isActive = true;
				} else {
					treeDataById[item].isActive = false;
				}
			}
		}
	},
	//获取职位
	getDutyArr: function (arr) {
		let dutyArr = [];
		arr.forEach((item) => {
			if (dutyArr.indexOf(item.position_name) === -1) {
				dutyArr.push(item.position_name);
			}
		});
		return dutyArr;
	},
	//获取选中的职位和部门的id
	getDutyDepartId: function (form, dutyName) {
		var obj;
		form.forEach((item) => {
			if (item.position_name !== undefined) {
				if (item.position_name === dutyName) {
					obj = item.position_id;
				}
			} else {
				if (item.department_name === dutyName) {
					obj = item.department_id;
				}
			}

		});
		return obj;
	},
	//搜索列表
	search: function (value1, value2, value3, arr) {
		var list = [];
		var val1 = new RegExp(value1, "g");
		var val2 = new RegExp(value2, "g");
		var val3 = new RegExp(value3, "g");
		arr.forEach((item) => {
			if ((!value1 || value1 === chooseAll.code[0]) && (value2 === "" && value3 === "")) {
				list = arr;
			} else if (val1.exec(item.position_name) !== null && (value2 === "" && value3 === "")) {
				list.push(item);
			} else if ((val1.exec(item.position_name) !== null && val2.exec(item.real_name) !== null) && value3 === "") {
				list.push(item);
			} else if ((val1.exec(item.position_name) !== null && val2.exec(item.real_name) !== null) && item.phone.indexOf(value3) > -1) {
				list.push(item);
			} else if ((val1.exec(item.position_name) !== null && item.phone.indexOf(value3) > -1) && value2 === "") {
				list.push(item);
			} else if ((!value1 || value1 === chooseAll.code[0]) && (val2.exec(item.real_name) !== null && item.phone.indexOf(value3) > -1)) {
				list.push(item);
			} else if ((!value1 || value1 === chooseAll.code[0]) && (value2 === "" && item.phone.indexOf(value3) > -1)) {
				list.push(item);
			} else if ((!value1 || value1 === chooseAll.code[0]) && (val2.exec(item.real_name) !== null && value3 === "")) {
				list.push(item);
			}
		});
		return list;
	},
	//获取总的条目数
	getTotal: function (testList) {
		if (testList !== undefined && testList.length > 0) {
			var tot = 0;
			for (var i = 0; i < testList.length; i++) {
				tot++;
			}
			return tot;
		}
	},
	//获取每页显示的相对应条目的内容
	getPageList: function (testList, limit, page) {
		if (testList !== undefined && testList.length > 0) {
			var tableList = [];
			tableList = testList.filter((item, index) => index < limit * page && index >= limit * (page - 1));
			return tableList;
		}
	},
	//对比出相应的权限
	isRight: function (array) {
		array.forEach((item) => {
			var arrId = [];
			var arrRight = [];
			var temp;
			for (var i in positionRight) {
				if (item.position_permission !== undefined) {
					temp = item.position_permission & positionRight[i];
					if (temp !== 0) {
						arrRight.push(positionRight.code[temp]);
						arrId.push(temp);
					}

					item["arrId"] = arrId;

					item["arrRight"] = arrRight;
					if ((item["arrId"][0] === positionRight.ALLBACKSTAGE && item["arrId"][1] == positionRight.ALLWEB) && item["arrId"].length == 2) {
						item["arrRight"] = ["全部"];
					} else if ((item["arrId"][0] === positionRight.ALLBACKSTAGE && item["arrId"][1] == positionRight.ALLWEB) && item["arrId"].length > 2) {
						item["arrId"][1] = "";
					} else if (item["arrId"][0] === positionRight.ALLWEB && item["arrId"].length > 1) {
						item["arrId"][1] = "";
					}
					item["arrRight"].forEach((item) => {
						if (item === positionRight.code[positionRight.ALLWEB]) {
							item = "";
						}
					});
				} else {
					item["arrId"] = 0;
					item["arrRight"] = chooseAll.code[0];
				}
			}
		});
		return array;
	},

	//列表渲染，以及后台发送数据时的判断
	/*
	 * //员工账号是否被冻结
	 * //员工账号是否绑定微信
	 * //员工是否是管理员
	 * //员工账号是否被删除
	//职位的权限的转换
	 * status状态0是获取数据1是向后台传递数据
	 * */
	judgeStaffList: function (rightArr, status) {
		var arr = [];
		for (var i in positionRight) {
			rightArr.forEach((item) => {
				if (item === positionRight.code[positionRight[i]]) {
					arr.push(positionRight[i]);
				}
			});
		}
		return arr;
	},
};
export const commonMethods = {
	//获取总的条目数
	getTotal: function (testList) {
		if (testList !== undefined && testList.length > 0) {
			var tot = 0;
			for (var i = 0; i < testList.length; i++) {
				tot++;
			}
			return tot;
		}

	},
	//获取每页显示的相对应条目的内容
	getPageList: function (testList, limit, page) {
		if (testList !== undefined && testList.length > 0) {
			var tableList = [];
			tableList = testList.filter((item, index) => index < limit * page && index >= limit * (page - 1));
			return tableList;
		}
	}
};


