<template>
	<div id="addNewDepartment">
		<el-dialog class="category-dialog" :visible="isDialogVisible" :show-close="false" size="">
			<div class="dialog-title" slot="title">添加部门</div>
			<div class="dialog-content">
				<div class="category clearfix">
					<div class="content-left left">
						<span>分类名称</span>
					</div>
					<div class="content-right left">
						<el-input class="left" v-model="dialongInputValue" placeholder="请输入内容"></el-input>
					</div>
				</div>

				<div class="button-group clearfix">
					<el-button class="ok-btn" @click="addConfirm">保存</el-button>
					<el-button class="cancel-btn" @click="addCancel">取消</el-button>
				</div>
			</div>
		</el-dialog>
	</div>
</template>

<script>
	import { staffDataGet } from "../../../api";
	import { treeMethods } from "../../../index";
	export default {
		props: ["isShowDialog", "allDepartName"],
		data() {
			return {
				dialongInputValue: "",
				isDialogVisible: false,
				departNameArr: []
			}
		},
		watch: {
			isShowDialog: function(val) {
				if(val === true) {
					this.isDialogVisible = true;
				}
			}

		},
		created() {
			this.departNameArr = this.allDepartName
		},
		methods: {
			//点击确定添加
			addConfirm() {
				if(this.dialongInputValue === "") {
					this.$slnotify({
			            message: "部门名称不能为空"
			       });
					return;
				} else if(this.departNameArr.indexOf(this.dialongInputValue) !== -1) {
						this.$slnotify({
			            message: "不能添加已经存在的部门名称"
			       });
					return;
				}
				let data = {
					department_save: 1,
					department_name: this.dialongInputValue,
				}
				staffDataGet.modifyDepartInfo(data, (resp) => {
					this.$emit("on-close",this.dialongInputValue);
					this.dialongInputValue = "";
					this.isDialogVisible = false;
				})

			},
			//点击取消添加
			addCancel() {
				this.handleClose()
			},

			//弹窗关闭前回调
			handleClose() {
				this.dialongInputValue = "";
				this.$emit("on-close");
				this.isDialogVisible = false;
			}
		}
	}
</script>

<style lang="scss" scoped>
	@import "src/styles/mixin.scss";
	#addNewDepartment {
		.category-dialog {
			/*top: 20%;*/
		}
		.dialog-title {
			@include wh(540px, 40px);
			@include fc(16px, #fff);
			background-color: #5a8cff;
			text-align: center;
			line-height: 40px;
		}
		.dialog-content {
			@include wh(540px, 200px);
		}
		.category {
			.content-left {
				@include wh(80px, 34px);
				line-height: 34px;
				margin-left: 60px;
			}
			.content-right {
				@include wh(320px, 34px);
			}
		}
		.button-group {
			width:100% ;
			margin: 80px 0 0 0;
		}
		.ok-btn {
			margin: 0 60px 0 77px;
			background-color: #5a8cff;
			color: #fff;
			cursor: pointer;
		}
	}
</style>

<style lang="scss">
	@import "src/styles/mixin.scss";
	#addNewDepartment {
		.el-input {
			width: 320px;
			margin-bottom: 30px
		}
		.el-input__inner {
			border-radius: 0;
			height: 34px;
		}
		.el-input__inner:hover {
			border: 1px solid #bfcbd9;
		}
		.el-input__inner:focus {
			border: 1px solid #bfcbd9;
		}
		.el-button {
			@include wh(160px, 40px);
			border: 1px solid #5a8cff;
			color: #5a8cff;
			border-radius: 5px;
			font-size: 14px;
			cursor: pointer;
		}
		.el-dialog {
			top: 20%;
		}
		.category-dialog .el-dialog__header {
			padding: 0;
			margin-bottom: 60px;
		}
		.el-dialog__body {
			padding: 0;
		}
	}
</style>


